.course-landing {
	width: 100%;
	color: $darkblue;
	margin-bottom: 5rem;

	&__info {
		width: 100%;
		background-color: $blue;
		padding: 3rem;

		&__name {
			font-size: 4.5rem;
			font-weight: 800;
			margin-bottom: 3rem;
		}

		&__tags {
			display: flex;
			flex-direction: row;
		    justify-content: flex-start;
		    align-items: flex-start;
		    flex-wrap: wrap;
		    gap: 3rem 2rem;

		    a {
		    	text-decoration: none;
		    	color: $darkblue;

		    	&:hover {
		    		text-decoration: underline;
		    	}
		    }
		}
	}

	&__description {
		width: 100%;
		padding: calc(3rem - 3px);
		border: 3px solid $blue;
		margin-bottom: 4rem;

		&__title {
			font-weight: 800;
			margin-bottom: 1rem;
		}
	}

	&__lessons {
		width: 100%;
		padding: calc(3rem - 3px);
		border: 3px solid $blue;

		ol li {
			margin-left: 3rem;
			font-weight: normal;
			margin-bottom: .7rem;
		}

		> ol:first-child > li{
			margin-left: 0;
			font-weight: 700;
		}
	}
}