.lesson-page {
	width: 100%;
	color: #2C3E50;
	font-size: 2.5rem;

	h1, h2, h3, h4, h5, h6 {
		margin-top: 1em;
		margin-bottom: 1em;
		font-weight: bold;
	}

	@for $i from 1 through 6 {
		h#{$i} {
			font-size: 5rem - $i*0.5rem;
		}
	}

	p {
		margin-bottom: 1.5rem;

	}

	strong {
		font-weight: bold;
	}

	em {
		font-style: italic;
	}

	blockquote {
		font-size: 1.375rem;
	    color: lighten(#2C3E50, 30%);
		margin: 0;
		padding-left: 1.5em;
		border-left: .5em #eee solid;
	}

	ol, ul {
	    margin: 1em 0 !important;
	    padding: 0 0 0 2em !important;

	    li {
	    	margin-bottom: .5em;
	    }
	}

	ol {
		list-style: decimal;
	}

	ul {
		list-style: disc;
	}

	pre {
		font-family: monospace, monospace;
		font-size: 1.75rem;
	    margin: 1.5em 0 1.5em 0;
	    padding: 1em;
	    white-space: pre;
	    white-space: pre-wrap;
	    word-wrap: break-word;
	    border: 1px solid #ddd;
	    background-color: #f8f8f8;
	    border-radius: 2px;

	    code {
	    	margin: 0;
		    padding: 0;
		    background: 0 0;
		    border: none;
	    }
	}

	hr {
		display: block;
	    height: 0;
	    border: 0;
	    font-style: italic;
	    border-bottom: 1px solid lighten(#2C3E50, 30%);
	    margin: 20px 0;
	    padding: 0;
	}

	a {
		color: lighten(#2C3E50, 20%);
		text-decoration: underline;

		&:focus {
			outline: thin dotted;
			color: $darkblue;
		}

		&:active {
			outline: 0;
			color: $darkblue;
		}
	}

	img {
		max-width: 100%;
		border: 0;
	    -ms-interpolation-mode: bicubic;
	    vertical-align: middle;

	    margin: 3rem 0;
	}
}